import React from 'react';
import { Link } from 'react-router-dom';

const Doctors = () => (
  <div style={styles.container}>
    <h1 style={styles.header}>Doctors List</h1>
    <p style={styles.description}>Manage and view doctors here.</p>
    <ul style={styles.list}>
      <li style={styles.listItem}>
        <Link to="/doctors/1" style={styles.link}>Dr. John Doe</Link>
      </li>
      <li style={styles.listItem}>
        <Link to="/doctors/2" style={styles.link}>Dr. Jane Smith</Link>
      </li>
      <li style={styles.listItem}>
        <Link to="/doctors/3" style={styles.link}>Dr. Emily Johnson</Link>
      </li>
    </ul>
    <button style={styles.addButton}>Add New Doctor</button>
  </div>
);

const styles = {
  container: {
    padding: '20px',
    backgroundColor: '#f4f7f6',
    minHeight: '100vh',
  },
  header: {
    fontSize: '32px',
    color: '#333',
    marginBottom: '10px',
  },
  description: {
    fontSize: '18px',
    color: '#555',
    marginBottom: '20px',
  },
  list: {
    listStyleType: 'none',
    padding: 0,
  },
  listItem: {
    marginBottom: '15px',
  },
  link: {
    fontSize: '18px',
    color: '#007bff',
    textDecoration: 'none',
    transition: 'color 0.3s',
  },
  addButton: {
    padding: '10px 20px',
    backgroundColor: '#28a745',
    color: 'white',
    border: 'none',
    borderRadius: '4px',
    cursor: 'pointer',
    marginTop: '20px',
    transition: 'background-color 0.3s',
  },
};

export default Doctors;
